  // tslint:disable-next-line: no-any
  public httpPost(
    inputData: any,
    actionName: string,
    url: any
  ): Observable<any> {
    // AES encryption
    // tslint:disable-next-line:max-line-length
    const data: string = CryptoJS.AES.encrypt(
      JSON.stringify(inputData),
      CryptoJS.enc.Base64.parse(environment.encryptionKey),
      { mode: CryptoJS.mode.ECB }
    ).toString();
    const formData = new FormData();
    formData.append('data', data);
    const backEndURL = url;
    if (actionName == 'get' || actionName == 'GET') {
      return this.http.get(backEndURL).pipe((data) => {
        let responseData: any = {};
        responseData = data;
        return responseData;
      });
    } else {
      return this.http
        .post(backEndURL, formData, {
          observe: 'response',
          responseType: 'text',
          // tslint:disable-next-line:no-shadowed-variable
        })
        .pipe(
          map((data) => {
            let responseData: any = {};
            let response: any;
            if (typeof data.body == 'string') {
              response = JSON.parse(
                CryptoJS.AES.decrypt(
                  data.body.replace(/^"(.*)"$/, '$1'),
                  CryptoJS.enc.Base64.parse(environment.decryptionKey),
                  { mode: CryptoJS.mode.ECB }
                ).toString(CryptoJS.enc.Utf8)
              );
            } else {
              response = data.body;
            }
            responseData = response;
            responseData.status = data.status;
            return responseData;
          })
        );
    }
  }
  /**
   * Expand all list row
   * @param listData
   */
  public expandContainerAll(listData: any) {
    if (this.expandAll.length == 0) {
      for (let i = 0; i < listData['default_data'].length; i++) {
        let flag: boolean = $('#expandContainer' + i).hasClass('d-none')
          ? true
          : false;
        this.expandAll.push(flag);
      }
    }
    if (this.expandAll.indexOf(false) == -1) {
      for (let i = 0; i < listData['default_data'].length; i++) {
        this.expandAll[i] = false;
        $('#expandclose' + i)
          .removeClass('icon-71-infoup')
          .addClass('icon-72-infodown');
      }
      $('.cls-sublist').removeClass('d-none').addClass('d-flex');

      listData.default_Parms.expand = true;
    } else {
      $('.cls-sublist').removeClass('d-flex').addClass('d-none');
      for (let i = 0; i < listData['default_data'].length; i++) {
        this.expandAll[i] = true;
        listData.default_Parms.expand = false;
        $('#expandclose' + i)
          .removeClass('icon-72-infodown')
          .addClass('icon-71-infoup');
      }
    }
  }

  public dashboardService(
    moduleName: string,
    inputData: any,
    actionName: string
  ): Observable<any> {
    // AES encryption
    const data: string = CryptoJS.AES.encrypt(
      JSON.stringify(inputData),
      CryptoJS.enc.Base64.parse(environment.encryptionKey),
      { mode: CryptoJS.mode.ECB }
    ).toString();
    const formData = new FormData();
    formData.append('data', data);
    let backEndURL =
      environment.BACKEND_URL + '' + this.chartModules[moduleName];
    return this.http
      .post(backEndURL, formData, {
        observe: 'response',
        responseType: 'text',
      })
      .pipe(
        map((data) => {
          let responseData: any = {};
          let response: any;
          if (typeof data.body == 'string') {
            response = JSON.parse(
              CryptoJS.AES.decrypt(
                data.body.replace(/^"(.*)"$/, '$1'),
                CryptoJS.enc.Base64.parse(environment.decryptionKey),
                { mode: CryptoJS.mode.ECB }
              ).toString(CryptoJS.enc.Utf8)
            );
          } else {
            response = data.body;
          }
          responseData = response;
          responseData.status = data.status;
          return responseData;
        })
      );
  }

